@import url('https://fonts.googleapis.com/css?family=Open+Sans|Roboto:300,400,500,700');

[contentEditable=true]:empty:not(:focus)::before{
    content: attr(data-ph);
    opacity: .7;
}

body {
    overflow: hidden; 
}

.App {
    font-family: "Roboto", 'Open Sans', sans-serif;
    font-weight: 500;
    /*text-transform: uppercase;*/
    background: #fafafa;
    display: -ms-flexbox;
    display: flex;
}

.Navbar {
    background: #3ba7f0;
    padding: .5rem 2rem;
    color: #fff;
    text-align: center;
    overflow-y: auto;
    /*box-shadow: 2px 3px 16px rgba(0, 0, 0, 0.35);*/
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Navbar .title {
    font-size: 3rem;
    /*text-shadow: 2px 3.464px 16px rgba(0, 0, 0, 0.35);*/
    font-weight: 500;
    cursor: default;
}

.Navbar .SearchBar {
    background: #3498db;
    height: 2rem;
    font-family: "Open Sans", sans-serif;
    font-size: 1.5rem;
    padding: .4rem 1rem;
    color: #fff;
    /*border-radius: .4rem;*/
    border-radius: .1rem;
    text-align: left;
    overflow: hidden;
}

.Navbar .date-link {
    font-family: 'Open Sans', sans-serif;
    font-size: 2rem;
    display: block;
    margin-top: 1rem;
    cursor: pointer;
    color: #cfd8dc;
    text-decoration: none;
    -webkit-transition: all .1s;
    -o-transition: all .1s;
    transition: all .1s;
}

.Navbar .date-link:hover {
    color: #fff;
}

.Navbar .date-link.disabled {
    color: rgba(255, 255, 255, 0.8);
    cursor: default;
}

.Navbar .date-link.disabled:hover {
    text-shadow: none;
}

.Main {
    padding: 1rem 4rem;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    max-height: 100vh;
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    z-index: 0;
    height: 100vh;
}

.Main .ClearButtons {
    float: right;
    margin-top: .5rem;
}

.Main .ClearButtons .btn {
    display: inline-block;
    background: #3ba7f0;
    padding: .4rem .8rem;
    /*border-radius: .4rem;*/
    border-radius: .1rem;
    color: #fff;
    font-weight: 500;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2);
            box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2);
    -webkit-transition: all .15s;
    -o-transition: all .15s;
    transition: all .15s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
    cursor: pointer;
}

.Main .ClearButtons .btn:hover {
    background: #47a4e2;
}

.Main .ClearButtons .btn.disabled {
    opacity: .6;
    cursor: default;
}

.Main .ClearButtons .btn.disabled:hover {
    background: #3498db;
}

.Main .ClearButtons .clear-history.btn {
    margin-right: 1rem;
}

.Main .no-history {
    position: relative;
    float: left;
    top: 10rem;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: rgba(0,0,0,.68);
    font-weight: 500;
    font-family: 'Open Sans', sans-serif;
    font-size: 2.5rem;
    cursor: default;
}

.Main .DateDiv .date {
    color: #212121;
    font-size: 2.5rem;
    cursor: default;
    margin: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Main .DateDiv:first-child .date {
    margin-top: 2rem;
}

.Main .DateDiv .sites {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 2rem 0;
}

.Main .DateDiv .sites .Site {
    display: inline-block;
    margin: .5rem 4rem 3.5rem 0;
    position: relative;
    cursor: pointer;
    text-align: center;
    color: #5a5a5a;
    font-family: "Open Sans", sans-serif;
    font-weight: 500;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Main .DateDiv .sites .Site:last-child {
    margin-right: 0;
}

.Main .DateDiv .sites .Site .circle {
    background: #fff;
    border-radius: 50%;
    height: 10rem;
    width: 10rem;
    display: inline-block;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    border: 1px solid #c3c3c3;
    background-position: 50%;
    background-size: 100% 60%;
    background-repeat: no-repeat;
    position: relative;
    overflow: hidden;
}

.Main .DateDiv .sites .Site:hover .circle {
    -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
            box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
    border: 1px solid transparent;
    /*border-radius: 2rem;*/
    border-radius: .1rem;
    background-size: 150% 90%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
    border-radius: 0;
}

.Main .DateDiv .sites .Site .circle .title {
    max-height: 3rem;
    overflow: hidden;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Main .DateDiv .sites .Site:hover .circle .title {
    top: -1rem;
}

.Main .DateDiv .sites .Site .circle .time {
    overflow: hidden;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
}

.Main .DateDiv .sites .Site:hover .circle .time {
    bottom: -.5rem;
}

.Main .DateDiv .sites .Site .info {
    display: block;
    width: 100%;
    height: 0;
    background: #4ca9e7;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    overflow: hidden;
    z-index: -5;
    position: absolute;
    top: 4rem;
}

.Main .DateDiv .sites .Site:hover .info {
    top: 8rem;
    height: auto;
    /*border-radius: 0 0 2rem 2rem;*/
    border-radius: 0 0 .1rem .1rem;
    padding: .2rem 0 .1rem;
    border-radius: 0;
}

.Main .DateDiv .sites .Site .info .domain {
    color: #fff;
    opacity: 0;
    margin-top: 2.5rem;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    overflow: hidden;
    border: 1px solid transparent;
    border-width: 0 .6rem;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.Main .DateDiv .sites .Site:hover .info .domain {
    opacity: 1;
}

/**********************
******** Modal ********
**********************/

.Modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

.Modal.show {
    display: block;
    -webkit-animation-name: animateshow;
    -webkit-animation-duration: .4s;
    animation-name: animateshow;
    animation-duration: .4s;
}

.Modal.hide {
    -webkit-animation-name: animatehide;
    -webkit-animation-duration: .4s;
    animation-name: animatehide;
    animation-duration: .4s
}

.Modal .modalContent {
    position: relative;
    margin: auto;
    padding: 0;
    border: 1px solid #888;
    border-radius: .1rem;
    width: 30%;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
    top: 5%;
    overflow: hidden;
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: .4s;
    animation-name: animatetop;
    animation-duration: .4s
}

.Modal.hide .modalContent {
    top: -300px;
    -webkit-animation-name: animateup;
    -webkit-animation-duration: .4s;
    animation-name: animateup;
    animation-duration: .4s
}

.Modal .modalHeader {
    padding: .5rem 1rem;
    background: #3ba7f0;
    color: #fff;
}

.Modal .modalHeader h2 {
    padding: .2rem 0;
    margin: 0;
}

.Modal .modalHeader .close {
    color: #cfd8dc;
    float: right;
    font-size: 1.6rem;
    font-weight: bold;
    -webkit-transition: all .15s;
    -o-transition: all .15s;
    transition: all .15s;
}

.Modal .modalHeader .close:hover,
.Modal .modalHeader .close:focus {
    color: #fff;
    text-decoration: none;
    cursor: pointer;
}

.Modal .modalBody {
    background-color: #fefefe;
    padding: .4rem .8rem;
}

.Modal .modalBody p {
    margin: .5rem;
}

.Modal .modalFooter {
    background-color: #fefefe;
    padding: .2rem .8rem 1rem;
    color: #fff;
    text-align: right;
}

.Modal .modalFooter .button {
    background: #3ba7f0;
    color: #fff;
    padding: .4rem .8rem;
    border: 0;
    /*border-radius: .4rem;*/
    border-radius: .1rem;
    margin-right: .5rem;
    -webkit-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2);
            box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12),0 1px 5px 0 rgba(0,0,0,0.2);
    -webkit-transition: all .15s;
    -o-transition: all .15s;
    transition: all .15s;
    cursor: pointer;
}

.Modal .modalFooter .button:hover {
    background: #3f9fe0;
}

.Modal .modalFooter .button.cancel {
    background: #fff;
    color: #212121;
}

.Modal .modalFooter .button.cancel:hover {
    background: #efefef;
}

/**********************
****** Animations *****
**********************/

/* Animate modalContent  */

@-webkit-keyframes animatetop {
    from {top: -300px; opacity: 0}
    to {top: 5%; opacity: 1}
}

@keyframes animatetop {
    from {top: -300px; opacity: 0}
    to {top: 5%; opacity: 1}
}

@-webkit-keyframes animateup {
    from {top: 5%; opacity: 1}
    to {top: -300px; opacity: 0}
}

@keyframes animateup {
    from {top: 5%; opacity: 1}
    to {top: -300px; opacity: 0}
}

/* Animate Modal  */

@-webkit-keyframes animateshow {
    from {display: none; opacity: 0}
    to {display: block; opacity: 1}
}

@keyframes animateshow {
    from {display: none; opacity: 0}
    to {display: block; opacity: 1}
}

@-webkit-keyframes animatehide {
    from {display: block; opacity: 1}
    to {display: none; opacity: 0}
}

@keyframes animatehide {
    from {display: block; opacity: 1}
    to {display: none; opacity: 0}
}


